@import url('../abstract/_variables.less');

.start-screen {
  background: url("img/start-screen/noise-transparent-2.png"), radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%), #211F20;
  background-repeat: repeat;

  &__wrapper {
    display: flex;
  }
  
  &__desc {
    padding-top: 163px;
    display: flex;
    flex-direction: column;
    padding-right: 42px;
    width: 460px;
  }

  &__heading {
    margin-bottom: 42px;
    color: @color-light-xl;
  }

  &__subheading {
    margin-bottom: 42px;
    color: @color-dark-s;
  }

  &__img {
    width: 698px;
    img {
      display: block;
      width: 100%;
    }
  }

}